.todosPage {
    align-self: flex-start;
    width: 100%;
    height: 100%;

    .todosPageHeader {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
    }

    .todosPageHeading {
        font-size: 20px;
    }

    .todosPageNotFound {
        text-align: center;
        margin-top: 20px;
        font-size: 24px;
        font-weight: 700;
    }

    .todosPageSearchBar {
        margin-left: 40px;
        width: 50%;
    }

    .todosPageNewTodoButton {
        margin-left: auto;
    }

    .todosPageSort {
        margin-left: 20px;
    }

    a {
        text-decoration: none;
        transition-property: opacity;
        transition-duration: 0.3s;
        transition-timing-function: ease-in-out;

        &:hover {
            opacity: 0.7;
        }
    }
}
